import React from "react";
import { CountdownRenderProps } from "react-countdown";
import clsx from "clsx";

export const CountdownRenderer = ({
  days,
  hours,
  minutes,
  seconds,
  props,
}: CountdownRenderProps) => {
  return (
    <div
      className={clsx(props.className, {
        ["width"]: days > 0,
      })}>
      {days > 0 ? (
        <div>
          {days}
          <span>DAYS</span>
        </div>
      ) : null}
      <div>
        {hours}
        <span>HOURS</span>
      </div>
      <div>
        {minutes}
        <span>MIN</span>
      </div>
      <div>
        {seconds}
        <span>SEC</span>
      </div>
    </div>
  );
};
